<template>
  <div class="apply_book">
    <h2>入党申请书</h2>
    <div><input type="file" @change="postImg()" id="img_file" name="img_file"></div>
    <div class="img" >
      <div v-for="(item, index) in data" :key="index" ><img :src="item.img_src" width="100px"><i class="fa fa-trash" aria-hidden="true" @click="deleteImg(item.id)"></i></div>

    </div>
  </div>
</template>

<script>
export default {
  name: "apply_book",
  data (){
    return {
      data: '',
      img_type: 1
    }
  },
  methods:{
    postImg(){
      const img = document.getElementById('img_file').files[0]
      var imgname = document.getElementsByName("img_file")[0].files[0]
      console.log(img);
      console.log(imgname);
      const than = this
      // const file = e.target.files[0]
      const param = new FormData()
      param.append('img_file', img)
      param.append('img_type', 1)
      console.log(param.get('img_file'));
      let config = {
        headers: {'Content-Type': 'multipart/form-data'}
      }
      than.axios.post('http://127.0.0.1:8000/img/',param, config).then(
        res => {
          console.log(res.status);
          if (res.status === 200){
            than.getImg()
          }
        }
      )
    },
    getImg(){
      const than = this
      than.axios.get('http://127.0.0.1:8000/img/').then(
        res => {
          if (res.status === 200){
            than.data = res.data
            console.log(than.data);
          }
        }
      )
    },
    deleteImg(id){
      const than = this
      than.axios.delete('http://127.0.0.1:8000/img/'+id+'/').then(
        res => {
          if (res.status == 204){
            alert('删除成功')
            than.getImg()
          }
        }
      )
    }

  },
  created (){
    this.getImg()
  }
}
</script>

<style scoped>
.apply_book
{
  position: relative;
}
.img
{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;

}
.img div
{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.img img
{
  margin-bottom: 20px;
}
.img div i
{
  width: 25px;
  height: 25px;
  font-size: 25px;
  line-height: 25px;
  cursor: pointer;
  margin-left: 25px;
}
.img div i:hover
{
  color: tomato;
}
</style>
